.card-default, .profile-sidebar, .card-list, .card-inverted, footer, nav {
  box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.1); }

.card-default, .profile-sidebar, .card-list, .card-inverted {
  border-radius: 5px;
  padding: 30px;
  margin: 5px 0px; }
  .card-default ul, .profile-sidebar ul, .card-list ul, .card-inverted ul {
    margin-bottom: 0px; }
  @media (max-width: 576px) {
    .card-default, .profile-sidebar, .card-list, .card-inverted {
      padding: 20px; } }

.card-default, .profile-sidebar, .card-list {
  background-color: #f8f9fa; }

.card-inverted {
  background-color: #101010;
  color: #f8f9fa; }

footer {
  padding: 30px;
  background-color: #f8f9fa;
  text-align: center; }
