    list-style-type: none;
    padding: 0;
    display: inline-block; }

nav {
  min-width: 100%;
  background-color: #f8f9fa; }

.navbar-brand img {
  max-height: 50px; }

.nav-link {
  color: #888888;
  text-decoration: none;
  padding: 4px; }
  .nav-link:hover {
    color: #101010; }

.profile-sidebar {
  float: left;
  margin-right: 5px;
  width: 95%;
  min-width: 280px;
  max-width: 300px;
  position: relative; }
  @media (max-width: 768px) {
    .profile-sidebar {
      margin-left: 10px; } }
  .profile-sidebar ul {
    list-style-type: none; }
  .profile-sidebar h6 {
    font-size: 1rem; }
    @media (max-width: 768px) {
      .profile-sidebar h6 {
        font-size: 1rem; } }
    @media (max-width: 576px) {
      .profile-sidebar h6 {
        font-size: 1rem; } }

.profile-picture {
  width: 50%;
  min-width: 50px;
  max-width: 100px;
  max-height: 100px;
  border-radius: 50%;
  float: left;
  vertical-align: middle; }

.profile-info {
  float: right;
  text-align: right;
  width: 45%;
  min-width: 120px; }

.card-list ul {
  list-style-type: none;
  position: relative;
  left: 20px; }

.card-list .profile-picture {
  max-height: 75px;
  max-width: 75px; }

.call-to-action, form .btn.btn-primary, .contact_form .btn.btn-primary {
  display: inline-block;
  color: #f8f9fa;
  background-color: purple;
  border-radius: 5px;
  padding: 10px 20px;
  text-decoration: none;
  transition: .2s ease-in-out;
  font-weight: bold;
  letter-spacing: 1px; }
  .call-to-action:hover, form .btn.btn-primary:hover, .contact_form .btn.btn-primary:hover {
    text-decoration: none;
    background-color: blue;
    color: #f8f9fa;
    transition: .2s ease-in-out; }

.title {
  margin-bottom: 10px;
  margin-bottom: 10px;
  font-size: 1.2rem; }
  @media (max-width: 768px) {
    .title {
      font-size: 1.08rem; } }
  @media (max-width: 576px) {
    .title {
      font-size: 1rem; } }

.h1-logo {
  height: 4.29982rem;
  display: inline-block; }

form label, .contact_form label {
  font-weight: bold;
  margin-bottom: 3px;
  display: block; }

form input, .contact_form input, form textarea, .contact_form textarea {
  margin-bottom: 10px; }

form .btn.btn-primary:hover, .contact_form .btn.btn-primary:hover {
  background-color: blue; }

main {
  max-width: 1140px;
  width: 100%; }
  @media (max-width: 1140px) {
    main {
      max-width: 992px; } }
  @media (max-width: 992px) {
    main {
      max-width: 768px; } }
  @media (max-width: 768px) {
    main {
      max-width: 576px; } }

.content {
  width: calc(100% - 305px);
  min-width: 315px;
  margin-left: auto;
  margin-right: auto;
  overflow: hidden; }

.content.unauthenticated_content {
  display: block;
  width: 100%;
  float: none; }

footer {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  width: 100%;
  margin-top: 30px; }
  footer section {
    min-width: 300px; }

.social-list {
  display: flex;
  justify-content: space-around;
  padding: 0px; }
  .social-list li {
    display: inline-block;
    float: left; }
