    .profile-picture.detail {
      float: none;
      margin-left: auto;
      margin-right: auto; } }

.profile-detail {
  display: block;
  position: relative;
  left: 30px;
  width: calc(100% - 30px); }
  @media (max-width: 768px) {
    .profile-detail {
      display: static;
      left: 0px;
      top: 30px; }
      .profile-detail br {
